<template>
  <div class="pt-2">
    <div style="position: fixed; top: 50%; left: 50%; z-index: 999;">
      <atom-spinner
        v-if="loading"
        :animation-duration="speed"
        :size="config.size"
        :color="spinnersColor"
      />
    </div>
    <div class="row">
      <div class="flex xs12 md6">      
        <div
          class="title mb-3"
          :style="computedStylesTitle"
        >
          {{$t('dashboard.tabs.billingAddress.personalInfo')}}
        </div>
        <va-input
          :label="$t('dashboard.tabs.billingAddress.firstName')"
          v-model="form.name"
          :readonly = "true"
        />
        <va-input
          :label="$t('dashboard.tabs.billingAddress.email')"
          v-model="form.email"
          :readonly = "false"
          :error="!!emailErrors.length"
          :error-messages="emailErrors"
        />
        <!-- <va-input
          :label="$t('dashboard.tabs.billingAddress.id_no')"
          v-model="form.id_no"
          :readonly = "true"
        /> -->
        <va-input
          :label="$t('dashboard.tabs.billingAddress.phone_no')"
          v-model="form.phone_no"
          :readonly = "false"
          :error="!!phoneNoErrors.length"
          :error-messages="phoneNoErrors"
        />
      </div>
      <div class="flex xs12 md6">
        <div
          class="title mb-3"
          :style="computedStylesTitle"
        >
          {{$t('dashboard.tabs.billingAddress.saccoInfo')}}
        </div>
        <va-input
          label="Customer number"
          v-model="form.member_no"
          :readonly = "true"
        />
        <!-- <va-checkbox
          :label="$t('dashboard.tabs.billingAddress.infiniteConnections')"
          v-model="form.connection"
        /> -->
        <div class="row mt-5 pull-right">            
          <va-button @click="onsubmit" >
            {{ $t('forms.controls.update') }}
          </va-button>                        
        </div>
      </div> 
    </div>
    <!-- <div class="row justify--center">
      <va-button @click="submit">
        {{$t('dashboard.tabs.billingAddress.addConnection')}}
      </va-button>
    </div> -->
  </div>
</template>

<script>
import axios from 'axios'
import * as spinners from 'epic-spinners'
export default {
  components: {
    ...spinners      
  },
  name: 'billing-address-tab',
  data () {
    return {
      userData: JSON.parse(localStorage.getItem('LoggedUser')),      
      token: JSON.parse(localStorage.getItem('Token')), 
      loading: false,     
      form: {
        name: '',
        email: '',
        id_no: '',
        phone_no: '',
        member_no: '',
        country: '',
        connection: true,
        user:'',
      },
      emailErrors : [],
      phoneNoErrors: [],
      config: {
        size: 80,
        duration: 1500,
      },
      spinnersColor: this.$themes.secondary,
      currentDuration: 1500,
      sliderDuration: {
        min: 1000,
        max: 2000,
      },
    }
  },
  computed: {
    speed () {
      return this.sliderDuration.min + this.sliderDuration.max - this.currentDuration
    },

    computedStylesTitle () {
      return {
        color: this.$themes.dark,
      }
    },

    formReady () {
      return !this.phoneNoErrors.length && !this.emailErrors.length
    },

  },
  methods: {
    onsubmit () {      
      this.emailErrors = this.form.email ? [] : ['Email is required']
      this.phoneNoErrors = this.form.phone_no  ? [] : ['Phone No is required']
      if (this.formReady) { 
        this.loading = true
        var self = this

        let config = {
          headers: {
            Authorization: this.userData.user_id+' '+this.token
          }
        }        
        axios.post(`${this.$apiUrl}UpdateCustDetails`, this.form, config)
        .then(function (res) {
          self.loading = false

        })
        .catch(function (error) {
          self.loading = false
          console.log(error)
        })        
      }
    }, 
  },


  created () {
    this.userData = JSON.parse(localStorage.getItem('LoggedUser'))
    this.form.name = this.userData.full_name
    this.form.email = this.userData.user_email
    this.form.email = this.userData.user_email
    this.form.id_no = this.userData.id_number
    this.form.phone_no = this.userData.mobile_number
    this.form.member_no = this.userData.member_number
    this.form.user = this.userData.user_id
  },
}
</script>
