<template>
  <va-list fit class="mb-2">
    <va-list-label>
      Invoices
    </va-list-label>
    <template v-for="(kin, i) in kins" v-if="i <= 8">
        <va-item>
          <va-item-section avatar>
            <avatar :username="kin.inv_date | formatLongDate1" :color="spinnersColor"></avatar>
          </va-item-section>
          <va-item-section>
            <va-item-label>
              Invoice# {{ kin.inv_number }}
            </va-item-label>

            <va-item-label caption>
              {{ kin.inv_date | formatDate }}
            </va-item-label>

            <va-item-label>
             {{ kin.inv_amount | Currency }}
            </va-item-label>
          </va-item-section>

          <va-item-section side v-if="kin.isbeneficiary">
            <va-icon name="fa fa-check"/>
          </va-item-section>
        </va-item>
    </template>
  </va-list> 
</template>

<script>
import axios from 'axios'
import Avatar from 'vue-avatar'

export default {
  components: {
    Avatar,
  },
  data () {
    return {
      trans: {},
      loading: false,
      spinnersColor: this.$themes.primary,
      count: 0,
      kins:{},
      term: null,
      mode: 0,
      userData: JSON.parse(localStorage.getItem('LoggedUser')),
      token: JSON.parse(localStorage.getItem('Token')),
    }
  },
  created () {
    this.getInvoices()
  }, 
  
  methods: {
    getInvoices () {
      const self = this

      let config = {
        headers: {
          Authorization: this.userData.user_id+' '+this.token
        }
      }

      axios.post(`${self.$apiUrl}getinvoices`, this.userData.user_id, config)
        .then(function (res) {
          res = res.data 
          self.kins = res      
        })
        .catch(function (error) {
          console.log(error)
        })
    }    
  },
}
</script>

